@import url(https://fonts.googleapis.com/css2?family=Hind+Siliguri&family=Ubuntu:wght@500&display=swap);
html{
  scroll-behavior:smooth;
  box-sizing:border-box;
  font-size:100%;
  --color-accent-red: #b82828ef;
  --color-accent-red-hover: #8f1515ef;
  --color-bg-light: #fcd23c25;
  --color-white: #fcfafa;
  --color-bg-yellow: #fcd23ca8;
  --color-grey-text: #383838
}

*,*:before,*:after{
  box-sizing:inherit;
  margin:0
}

body{
  position:relative;
  margin:0;
  padding:0;
  font-family:"Hind Siliguri",Arial,Helvetica,sans-serif;
  font-size:1.5rem;
  font-weight:400;
  line-height:1.6;
  color:#383838;
  color:var(--color-grey-text)
}

section{
  padding:4rem
}

h1{
  font-size:4rem
}

h2{
  margin:.5rem 0
}

p{
  padding:.5rem 0
}

.link{
  font-family:Ubuntu;
  color:#b82828ef;
  color:var(--color-accent-red);
  text-decoration:none
}

.link:hover{
  color:#8f1515ef;
  color:var(--color-accent-red-hover)
}

.sr-only{
  position:absolute;
  left:-10000px;
  top:auto;
  width:1px;
  height:1px;
  overflow:hidden
}

.hero::before{
  content:"";
  position:absolute;
  width:100%;
  height:100%;
  left:0;
  top:0;
  background-size:cover;
  background-repeat:no-repeat;
  background-position:center;
  background-image:url(/front-end-foxes/reactapp/portfolio/static/media/hero_bg.b033fd1c.jpg);
  z-index:-1;
  opacity:.5
}

.hero{
  min-height:100vh;
  position:relative;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:row;
  flex-direction:row;
  -webkit-justify-content:space-around;
  justify-content:space-around;
  -webkit-flex-wrap:wrap;
  flex-wrap:wrap;
  -webkit-align-items:center;
  align-items:center
}

.hero__img{
  border:5px solid #fcfafa;
  border:5px solid var(--color-white);
  border-radius:10% 10% 10% 58%/10% 10% 10% 56%;
  max-height:30rem
}

.hero__container{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
  flex-direction:column;
  -webkit-justify-content:center;
  justify-content:center;
  -webkit-align-items:flex-start;
  align-items:flex-start
}

.hero__container__intro{
  font-size:2rem;
  letter-spacing:4px;
  font-weight:bold;
  color:#b82828ef;
  color:var(--color-accent-red)
}

.hero__container__cta{
  margin:1rem 0;
  padding:1rem 1.5rem;
  font-size:1.5rem;
  color:#fff;
  border-radius:6px;
  letter-spacing:1px;
  font-weight:bold;
  border:none;
  background-color:#b82828ef;
  background-color:var(--color-accent-red);
  cursor:pointer
}

.hero__container__cta:hover{
  opacity:.8
}

.about{
  background-color:#fcd23c25;
  background-color:var(--color-bg-light)
}

.about__tech-stack{
  border-radius:6px;
  background-color:#b82828ef;
  background-color:var(--color-accent-red);
  color:#fcfafa;
  color:var(--color-white);
  padding:.2rem .75rem;
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
  text-align:center
}

.projects{
  background-color:#fcd23ca8;
  background-color:var(--color-bg-yellow);
  text-align:center
}

.projects__all{
  margin:.5rem;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-wrap:wrap;
  flex-wrap:wrap;
  -webkit-justify-content:space-evenly;
  justify-content:space-evenly;
  -webkit-align-items:center;
  align-items:center
}

.project{
  margin:1rem;
  padding:1rem;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
  flex-direction:column;
  -webkit-align-items:center;
  align-items:center;
  max-width:30rem
}

.project__img{
  padding:.5rem;
  max-width:30rem;
  max-height:22rem
}

.project__tech-stack{
  font-weight:bold
}

.contact{
  text-align:center;
  background-color:#fcd23c25;
  background-color:var(--color-bg-light)
}

.footer{
  background-color:#fcd23ca8;
  background-color:var(--color-bg-yellow);
  display:-webkit-flex;
  display:flex;
  -webkit-flex-wrap:wrap;
  flex-wrap:wrap;
  -webkit-justify-content:center;
  justify-content:center;
  -webkit-align-items:center;
  align-items:center
}

.footer__link{
  margin:1rem .25rem;
  padding:1rem .25rem
}

@media(max-width: 850px){
  section{
    padding:1.5rem
  }

  .hero{
    -webkit-flex-direction:column;
    flex-direction:column
  }

  .hero__container{
    text-align:center;
    -webkit-align-items:center;
    align-items:center;
    margin-bottom:1.5rem
  }

  .hero__img{
    max-height:20rem
  }

  .project__img{
    max-width:20rem
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

